@import "../../../../variables";

.location__info {
	width: 325px;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	margin-bottom: 50px; }
.style-input {
	position: relative;
	label {
		font-size: 14px;
		font-weight: 300;
		margin-bottom: 10px; }
	input {
		margin-left: 10px;
		padding: 3px;
		width: 224px;
		border: 0;
		font-size: 14px;
		font-weight: 300;
		background: transparent;
		border-bottom: 1px solid $GRAY;
		&:focus {
			outline: none; } } }

.location__city-list {
	width: 224px;
	height: 106px;
	overflow: auto;
	right: 0;
	background: #ffffff;
	border: 1px solid $LGRAY;
	position: absolute;
	z-index: 10000;
	ul {
		margin: 0;
		padding-left: 5px; }
	li {
		font-size: 14px;
		padding: 5px;
		letter-spacing: 0.4px;
		font-weight: 300;
		list-style-type: none;
		color: $GRAY;
		&:hover {
			color: $GREEN; } } }
.svg__wrapper {
	position: absolute;
	cursor: pointer;
	top: 5px;
	right: 5px;
	svg {
		color: $BLACK;
		width: 9px; } }
iframe {
	margin-top: 20px; }

::-webkit-scrollbar-button {
	background-repeat:no-repeat {}
	width: 5px;
	height: 0px; }


::-webkit-scrollbar-track {
	border-right: 1px solid $LGRAY;
	border-left: 1px solid $LGRAY; }


::-webkit-scrollbar-thumb {
	-webkit-border-radius: 0px;
	border: 1.5px solid #fff;
	border-radius: 6px;
	background-color: $GRAY; }


::-webkit-scrollbar-thumb:hover {
	background-color: $GREEN; }


::-webkit-resizer {
	background-repeat:no-repeat {}
	width: 4px;
	height: 0px; }


::-webkit-scrollbar {
	width: 7px; }
